import formValidate from '@/utils/validate'
import {
	login
} from '@/api/user'
import {
	mapActions,
	mapGetters
} from 'vuex';
export default {
	data() {
		return {
			loginData: {
				account: '',
				password: ''
			},
			rule: [{
					name: 'account',
					checkType: 'phoneno',
					errorMsg: '请填写正确的手机号'
				},
				{
					name: 'password',
					checkType: 'notnull',
					errorMsg: '请填写密码'
				}
			],
			loginBtn: true,
			bottom: 0
		}
	},
	computed: {
		...mapGetters({
			mobile: 'user/mobile'
		})
	},
	created() {
		//赋值手机号
		this.loginData.account = this.mobile
	},
	methods: {
		...mapActions('user', ['setLoginStatus', 'setUserInfo', 'setMobile']),
		//登录处理
		handleLogin() {
			let valid = formValidate.check({ ...this.loginData
			}, this.rule);
			if (valid) {
				login(this.loginData).then(res => {
					if (res.code === 500) {
						uni.showToast({
							icon: 'none',
							title: '手机号或密码错误'
						});
					}
					if (res.code === 200) {
						//获取菜单列表
						//设置登录状态
						this.setLoginStatus(true);
						//保存用户信息到vuex
						this.setUserInfo(res.data);
						this.setMobile(res.data.mobile)
						//缓存用户信息
						uni.setStorageSync('userInfo', res.data);
						//跳转首页
						this.$Router.pushTab({
							name: 'index'
						})
					}
				}).catch(erro => {
					console.log(erro)
				})
			} else {
				uni.showToast({
					title: formValidate.error,
					icon: 'none'
				});
			}
		},
		inputFocus({
			detail
		}) {
			var that = this;
			uni.getSystemInfo({
				success(res) {
					that.bottom = detail.height
				}
			})
		},
		inputBlur() {
			this.bottom = 0
		}
	}
}
